import Vue from "vue";
import VueRouter from "vue-router";
// import { getUid, setToUrl, getStatus, removeUid, removeStatus } from '@/utils/auth'

Vue.use(VueRouter);

const router = new VueRouter({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "Home",
      component: () => import("@/views/home/index"),
      meta: { title: "首页" }
    }, {
      path: "/song_list",
      name: "SongList",
      component: () => import("@/views/songList/index"),
      meta: { title: "陕北民歌查询系统" }
    }, {
      path: "/player",
      name: "Player",
      component: () => import("@/views/player/index"),
      meta: { title: "播放视频" }
    }, {
      path: "/proverb_game",
      name: "ProverbGame",
      component: () => import("@/views/proverbGame/index"),
      meta: { title: "陕北民谚互动游戏" }
    }, {
      path: '/proverb_game/games',
      name: 'ProverbGames',
      component: () => import('@/views/proverbGame/games/index'),
      meta: { title: '互动游戏' }
    }, {
      path: "/tradition",
      name: "Tradition",
      component: () => import("@/views/tradition/index"),
      meta: {
        title: "传统音乐&曲艺"
      }
    }, {
      path: "/tradition/ditty",
      name: "Ditty",
      component: () => import("@/views/tradition/ditty/index"),
      meta: {
        title: "传统音乐"
      }
    }, {
      path: "/tradition/list",
      name: "traditionList",
      component: () => import("@/views/tradition/list/index"),
      meta: {
        title: "传统音乐列表"
      }
    }, {
      path: "/tradition/art",
      name: "Art",
      component: () => import("@/views/tradition/art/index"),
      meta: {
        title: "曲艺"
      }
    }, {
      path: "/tune",
      name: "Tune",
      component: () => import("@/views/tune/index"),
      meta: {
        title: "榆林小曲"
      }
    }, {
      path: "/tune/list",
      name: "TuneList",
      component: () => import("@/views/tune/list/index"),
      meta: {
        title: "榆林小曲列表"
      }
    }, {
      path: "/interaction",
      name: "Interaction",
      component: () => import("@/views/interaction/index"),
      meta: {
        title: "互动查询"
      }
    }, {
      path: "/interaction/dance",
      name: "InteractionDance",
      component: () => import("@/views/interaction/dance/index"),
      meta: {
        title: "传统舞蹈"
      }
    }, {
      path: "/interaction/arts",
      name: "InteractionArts",
      component: () => import("@/views/interaction/arts/index"),
      meta: {
        title: "传统美术"
      }
    }, {
      path: "/interaction/literature",
      name: "InteractionLiterature",
      component: () => import("@/views/interaction/literature/index"),
      meta: {
        title: "民间文学"
      },
    }, {
      path: "/interaction/folk",
      name: "InteractionFolk",
      component: () => import("@/views/interaction/folk/index"),
      meta: {
        title: "民俗"
      },
    }, {
      path: "/interaction/skill",
      name: "InteractionSkill",
      component: () => import("@/views/interaction/skill/index"),
      meta: {
        title: "传统技艺"
      },
    }
  ]
});

/* 路由异常错误处理，尝试解析一个异步组件时发生错误，重新渲染目标页面 */
router.onError(error => {
  const pattern = /Loading chunk (\d)+ failed/g;
  const isChunkLoadFailed = error.message.match(pattern);
  const targetPath = router.history.pending.fullPath;
  if (isChunkLoadFailed) {
    router.replace(targetPath);
  }
});

// 获取原型对象上的push函数
const originPush = router.push
// 修改原型对象上的push函数
router.push = function push(location) {
  return originPush.call(this, location).catch(err => err)
}

router.beforeEach((to, from, next) =>{
  if(to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

export default router;
